import { NGL3dApp } from '..';

export class Renderer {
    app: NGL3dApp;
    constructor(app: NGL3dApp) {
        this.app = app;
    }
    render(gl: WebGL2RenderingContext) {
        // console.log(gl);

        // console.log(this.app.scene.triList);

        this.app.scene.geometries.forEach((e) => { e.render(gl); });
    }
}