import { NGL3dApp } from '..';

// import { Point } from '../Geometry/Point';
import { GeometryBase } from '../Geometry/Core/Base';

export class Scene {
    NGL3dApp: NGL3dApp;

    geometries: GeometryBase[] = [];

    constructor(NGL3dApp: NGL3dApp) {
        this.NGL3dApp = NGL3dApp;
    }
    add(mesh: GeometryBase) {
        this.geometries.push(mesh);
    }
}